<template>
    <div class="rih-widget__show-page" id="rih-showpage">
        <header class="rih-header rih-back">
            <span class="rih-back__btn" @click="$emit('back')">
                <span class="rih-back__rect"></span>
                Вернуться назад
            </span>
        </header>
        <main class="rih-main">
            <div class="rih-main__info rih-info">
                <div class="rih-info__img-wrapper">
                    <img
                        :src="MEDIA + pickedInventory.avatar"
                        :alt="pickedInventory.title"
                    />
                </div>
                <div class="rih-info__description">
                    <h5
                        class="rih-info__category"
                        v-if="pickedInventory.category"
                    >
                        {{ pickedInventory.category.title }}
                    </h5>
                    <h2 class="rih-info__heading">
                        {{ pickedInventory.title }}
                    </h2>
                    <h3 class="rih-info__secondary-heading">
                        Технические характеристики
                    </h3>
                    <div class="rih-info__features">
                        <span
                            v-for="option in pickedInventory.option"
                            :key="option.title + option.value"
                            class="rih-info__feature"
                        >
                            <i
                                v-if="option.title === 'speed'"
                                class="fa-solid fa-gauge-high"
                            ></i>
                            <i
                                v-else-if="option.title === 'boost'"
                                class="fa-regular fa-clock"
                            ></i>
                            {{ option.value }}
                            <template v-if="option.title === 'speed'"
                                >км/ч</template
                            >
                            <template v-else-if="option.title === 'boost'"
                                >сек</template
                            >
                        </span>
                    </div>
                    <div class="rih-info__tariffs">
                        <div class="rih-info__tariff-header">
                            <h3 class="rih-info__secondary-heading">
                                Стоимость
                            </h3>
                            <span
                                @click="pickedPeriod = 'weekdays'"
                                class="rih-info__tariff-heading rih-info__tariff-heading--ml"
                                :class="{
                                    'rih-info__tariff-heading--active':
                                        pickedPeriod === 'weekdays',
                                }"
                                >ПН-ЧТ</span
                            >
                            <span
                                @click="pickedPeriod = 'weekend'"
                                class="rih-info__tariff-heading"
                                :class="{
                                    'rih-info__tariff-heading--active':
                                        pickedPeriod === 'weekend',
                                }"
                                >ПТ-СБ-ВС</span
                            >
                        </div>
                        <div
                            class="rih-info__tariffs-list"
                            v-if="pickedPeriod === 'weekdays'"
                        >
                            <span
                                class="rih-info__tariff-date"
                                style="
                                    color: var(--widget-primary);
                                    font-weight: 700;
                                "
                            >
                                Дневной тариф
                            </span>
                            <span class="rih-info__tariff-price"> </span>

                            <template
                                v-for="price in pricesObject.WEEKDAY_DAY.values"
                            >
                                <span
                                    class="rih-info__tariff-date"
                                    :key="'p' + price.period + price.value"
                                >
                                    {{ humanize(price.period) }}
                                </span>
                                <span
                                    class="rih-info__tariff-price"
                                    :key="'v' + price.period + price.value"
                                >
                                    {{ price.value }} руб
                                </span>
                            </template>
                            <span
                                class="rih-info__tariff-date"
                                style="
                                    color: var(--widget-primary);
                                    font-weight: 700;
                                "
                            >
                                Ночной тариф
                            </span>
                            <span class="rih-info__tariff-price"> </span>

                            <template
                                v-for="price in pricesObject.WEEKDAY_NIGHT
                                    .values"
                            >
                                <span
                                    class="rih-info__tariff-date"
                                    :key="'p' + price.period + price.value"
                                >
                                    {{ humanize(price.period) }}
                                </span>
                                <span
                                    class="rih-info__tariff-price"
                                    :key="'v' + price.period + price.value"
                                >
                                    {{ price.value }} руб
                                </span>
                            </template>
                        </div>
                        <div
                            class="rih-info__tariffs-list"
                            v-if="pickedPeriod === 'weekend'"
                        >
                            <span
                                class="rih-info__tariff-date"
                                style="
                                    color: var(--widget-primary);
                                    font-weight: 700;
                                "
                            >
                                Дневной тариф
                            </span>
                            <span class="rih-info__tariff-price"> </span>

                            <template
                                v-for="price in pricesObject.WEEKEND_DAY.values"
                            >
                                <span
                                    class="rih-info__tariff-date"
                                    :key="'p' + price.period + price.value"
                                >
                                    {{ humanize(price.period) }}
                                </span>
                                <span
                                    class="rih-info__tariff-price"
                                    :key="'v' + price.period + price.value"
                                >
                                    {{ price.value }} руб
                                </span>
                            </template>
                            <span
                                class="rih-info__tariff-date"
                                style="
                                    color: var(--widget-primary);
                                    font-weight: 700;
                                "
                            >
                                Ночной тариф
                            </span>
                            <span class="rih-info__tariff-price"> </span>

                            <template
                                v-for="price in pricesObject.WEEKEND_NIGHT
                                    .values"
                            >
                                <span
                                    class="rih-info__tariff-date"
                                    :key="'p' + price.period + price.value"
                                >
                                    {{ humanize(price.period) }}
                                </span>
                                <span
                                    class="rih-info__tariff-price"
                                    :key="'v' + price.period + price.value"
                                >
                                    {{ price.value }} руб
                                </span>
                            </template>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
import humanizeDuration from "humanize-duration";
import moment from "moment";
import { mapState } from "vuex";

export default {
    name: "RihShowpage",
    props: {
        pickedInventory: Object,
    },
    data() {
        return {
            pickedPeriod: "weekdays",
        };
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
        }),

        prices() {
            return JSON.parse(JSON.stringify(this.pickedInventory.prices)).sort(
                (a, b) => a?.title > b?.title
            );
        },

        pricesObject() {
            const obj = {
                WEEKDAY_DAY: null,
                WEEKDAY_NIGHT: null,
                WEEKEND_DAY: null,
                WEEKEND_NIGHT: null,
            };

            this.prices.some((price) => {
                if (!price.title) return false;

                if (price.title.match(/.*будни.*/gi)) {
                    if (price.title.match(/.*день.*/gi)) {
                        obj.WEEKDAY_DAY = price;
                    } else if (price.title.match(/.*ночь.*/gi)) {
                        obj.WEEKDAY_NIGHT = price;
                    }
                } else if (price.title.match(/.*выходной.*/gi)) {
                    if (price.title.match(/.*день.*/gi)) {
                        obj.WEEKEND_DAY = price;
                    } else if (price.title.match(/.*ночь.*/gi)) {
                        obj.WEEKEND_NIGHT = price;
                    }
                }
            });

            return obj;
        },
    },
    methods: {
        humanize(period) {
            const settings = {
                language: "ru",
                delimiter: " ",
                units: ["w", "d", "h", "m"],
            };
            return humanizeDuration(
                moment.duration(period).asMilliseconds(),
                settings
            );
        },
    },
};
</script>

<style lang="scss">
#rih-widget .rih-back {
    justify-content: flex-start !important;
    color: #9a9eb5;

    &__btn {
        cursor: pointer;
    }

    &__rect {
        display: inline-block;
        height: 2px;
        width: 35px;
        background-color: #d9d9d9;
        margin-right: 20px;
        position: relative;
        top: -4px;
    }
}

#rih-widget .rih-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;

    &__img-wrapper,
    &__description {
        @media (max-width: 1050px) {
            width: 100%;
        }
    }

    &__img-wrapper {
        width: 30%;

        @media (max-width: 1050px) {
            width: 50%;
            margin-bottom: 30px;
        }

        @media (max-width: 450px) {
            width: 100%;
        }
    }

    &__description {
        background-color: #fff;
        max-width: 600px;
        padding: 40px;
        border-radius: 8px;
    }

    &__img-wrapper img {
        width: 100%;
        height: auto;
        display: block;
        margin: 0 auto;
    }

    &__heading {
        font-weight: 700;
        font-size: 32px;
        margin-top: 0;
        margin-bottom: 22px;
        color: var(--widget-text);
    }

    &__secondary-heading {
        font-size: 20px;
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 20px;
        color: var(--widget-text);
    }

    &__features {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 18px;
    }

    &__feature {
        min-width: 90px;
        max-width: 25%;
        width: 25%;
        margin-bottom: 12px;
        color: var(--widget-text);

        & i {
            color: var(--widget-primary);
        }
    }

    &__tariff-header {
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        & h3 {
            margin: 0;
        }

        @media (max-width: 500px) {
            flex-wrap: wrap;

            & .rih-info__secondary-heading {
                width: 100%;
            }
        }
    }

    &__tariff-heading {
        font-size: 16px;
        font-weight: 700;
        color: #dce3ed;
        cursor: pointer;
        margin-left: 40px;

        &--active {
            color: var(--widget-primary);
            cursor: default;
        }

        &--ml {
            margin-left: auto;

            @media (max-width: 500px) {
                margin-right: auto;
                margin-left: 0;
            }
        }
    }

    &__tariffs-list {
        display: grid;
        grid-template-columns: 1fr auto;
    }

    &__tariff {
        display: flex;
        justify-content: space-between;
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #dce3ed;
    }

    &__tariff-date {
        color: var(--widget-text);
        font-size: 16px;
    }

    &__tariff-price {
        color: var(--widget-primary);
        font-weight: 700;
        font-size: 16px;
    }

    &__tariff-date,
    &__tariff-price {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #dce3ed;

        @media (max-width: 450px) {
            font-size: 14px;
        }

        @media (max-width: 380px) {
            font-size: 12px;
        }
    }

    &__category {
        font-weight: 400;
        font-size: 16px;
        color: #9a9eb5;
        margin-bottom: 5px;
        margin-top: 0;
    }

    &__desc {
        line-height: 1.6;
        font-size: 16px;
    }

    &__price {
        margin-top: 30px;
    }

    &__price--mb {
        margin-bottom: 20px;
    }

    @media (max-width: 1050px) {
        flex-wrap: wrap;

        .info__description {
            margin-top: 36px;
        }
    }
}
</style>
