<template>
    <div class="rih-booking" id="rih-booking">
        <div class="rih-booking__center-wrapper">
            <div class="rih-booking__header">
                <h2 class="rih-booking__heading">Бронирование мотоцикла</h2>
                <div class="rih-booking__t-picker">
                    <span
                        v-for="price in prices"
                        :class="{
                            'rih-booking__t-picker-tariff--active':
                                activeTariff && activeTariff.id === price.id,
                        }"
                        :key="price.id"
                        class="rih-booking__t-picker-tariff"
                        @click="changeTariff(price)"
                        >{{ price.title }}</span
                    >
                    <!--                    @click="changeTariff(price)"-->
                </div>
            </div>

            <div class="rih-booking__wrapper">
                <div class="rih-booking__time">
                    <div class="rih-booking__calendar rih-calendar">
                        <date-range-picker
                            class="rih-booking__calendar-component"
                            :key="pickerKey"
                            ref="picker"
                            opens="inline"
                            :ranges="false"
                            auto-apply
                            single-date-picker="range"
                            v-model="range"
                            :locale-data="rangeLocale"
                            :date-format="dateFormat"
                            :min-date="minDate"
                            @update="changeRange"
                            @change-month="loadBooking"
                            :readonly="step === 'second'"
                        ></date-range-picker>
                        <div class="rih-calendar__time-picker rih-time-picker">
                            <div style="margin-right: 15px">
                                <span
                                    class="rih-calendar__label rih-time-picker__label"
                                >
                                    Время начала
                                </span>
                                <vue-timepicker
                                    close-on-complete
                                    manual-input
                                    hour-label="Часы"
                                    minute-label="Минуты"
                                    placeholder="Введите время"
                                    v-model="timeStartValue"
                                    @input="calculateRent"
                                    :hour-range="hourRangeStart"
                                    :minute-range="minuteRangeStart"
                                    :minute-interval="5"
                                    drop-direction="auto"
                                ></vue-timepicker>
                            </div>
                            <div>
                                <span
                                    class="rih-calendar__label rih-time-picker__label"
                                >
                                    Время конца
                                </span>
                                <vue-timepicker
                                    close-on-complete
                                    manual-input
                                    hour-label="Часы"
                                    minute-label="Минуты"
                                    placeholder="Введите время"
                                    v-model="timeEndValue"
                                    @input="calculateRent"
                                    :hour-range="hourRangeEnd"
                                    :minute-range="minuteRangeEnd"
                                    :minute-interval="5"
                                    drop-direction="auto"
                                    :disabled="isNightTariff"
                                ></vue-timepicker>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="rih-booking__info">
                    <template v-if="step === 'first'">
                        <h3 class="rih-booking__subheading">
                            Детали бронирования
                        </h3>
                        <div class="rih-booking__data">
                            <div class="rih-booking__inventory">
                                <span class="rih-booking__label">Мотоцикл</span>
                                <h6 class="rih-booking__title">
                                    {{ pickedInventory.title }}
                                </h6>
                            </div>
                            <div class="rih-booking__date">
                                <span class="rih-booking__label"
                                    >Начало аренды</span
                                >
                                <div class="rih-booking__rent-datetime">
                                    <span class="rih-booking__rent-date">
                                        <i class="fa-regular fa-calendar"></i>
                                        <span>{{ formatDate(timeStart) }}</span>
                                    </span>
                                    <span class="rih-booking__rent-time">
                                        <i class="fa-regular fa-clock"></i>
                                        <span>{{ formatTime(timeStart) }}</span>
                                    </span>
                                </div>
                            </div>
                            <div class="rih-booking__tariff">
                                <span class="rih-booking__label"
                                    >Выбранный тариф</span
                                >
                                <div class="rih-booking__badge-tariff">
                                    <template v-if="activeTariff == null">
                                        Выберите тариф
                                    </template>
                                    <template v-else>
                                        {{ activeTariff.title }}
                                    </template>
                                </div>
                            </div>
                            <div
                                class="rih-booking__date rih-booking__date--end"
                            >
                                <span class="rih-booking__label"
                                    >Конец аренды</span
                                >
                                <div class="rih-booking__rent-datetime">
                                    <span class="rih-booking__rent-date">
                                        <i class="fa-regular fa-calendar"></i>
                                        <span>{{ formatDate(timeEnd) }}</span>
                                    </span>
                                    <span class="rih-booking__rent-time">
                                        <i class="fa-regular fa-clock"></i>
                                        <span>{{ formatTime(timeEnd) }}</span>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <div class="rih-booking__second-step">
                            <div class="rih-booking__personal">
                                <h3 class="rih-booking__subheading">
                                    Данные арендатора
                                </h3>
                                <div class="rih-booking__form">
                                    <input
                                        type="text"
                                        class="rih-booking__form-input"
                                        placeholder="ФИО *"
                                        v-model="fio"
                                    />
                                    <vue-phone-number-input
                                        class="rih-phone-number-input"
                                        :preferred-countries="
                                            preferredCountries
                                        "
                                        :translations="{
                                            phoneNumberLabel: 'Телефон *',
                                        }"
                                        :color="colors.primary"
                                        valid-color="#1ab394"
                                        error-color="#ce352c"
                                        @update="changeNumber"
                                        v-model="phone"
                                    ></vue-phone-number-input>
                                    <input
                                        type="email"
                                        class="rih-booking__form-input"
                                        placeholder="E-mail *"
                                        v-model="email"
                                    />
                                </div>
                            </div>
                            <div class="rih-booking__rent-data">
                                <h3 class="rih-booking__subheading">
                                    Детали бронирования
                                </h3>
                                <div class="rih-booking__dates">
                                    <div
                                        class="rih-booking__date"
                                        style="margin-bottom: 20px"
                                    >
                                        <span class="rih-booking__label"
                                            >Начало аренды</span
                                        >
                                        <div class="rih-booking__rent-datetime">
                                            <span
                                                class="rih-booking__rent-date"
                                            >
                                                <i
                                                    class="fa-regular fa-calendar"
                                                ></i>
                                                <span>{{
                                                    formatDate(timeStart)
                                                }}</span>
                                            </span>
                                            <span
                                                class="rih-booking__rent-time"
                                            >
                                                <i
                                                    class="fa-regular fa-clock"
                                                ></i>
                                                <span>{{
                                                    formatTime(timeStart)
                                                }}</span>
                                            </span>
                                        </div>
                                    </div>
                                    <div class="rih-booking__date">
                                        <span class="rih-booking__label"
                                            >Конец аренды</span
                                        >
                                        <div class="rih-booking__rent-datetime">
                                            <span
                                                class="rih-booking__rent-date"
                                            >
                                                <i
                                                    class="fa-regular fa-calendar"
                                                ></i>
                                                <span>{{
                                                    formatDate(timeEnd)
                                                }}</span>
                                            </span>
                                            <span
                                                class="rih-booking__rent-time"
                                            >
                                                <i
                                                    class="fa-regular fa-clock"
                                                ></i>
                                                <span>{{
                                                    formatTime(timeEnd)
                                                }}</span>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </template>
                    <div class="rih-booking__footer">
                        <div class="rih-booking__total-wrapper">
                            <span class="rih-booking__label"
                                >Итоговая сумма</span
                            >
                            <span class="rih-booking__total"
                                >{{ sumRent
                                }}<i class="fa-solid fa-ruble-sign"></i
                            ></span>
                        </div>
                        <div
                            class="rih-promo-input rih-promo-input--desktop-show"
                        >
                            <input
                                v-model="promocode"
                                placeholder="Введите промокод"
                                type="text"
                                class="rih-promo-input__input"
                                :disabled="discount !== null"
                            />
                            <button
                                class="rih-promo-input__submit"
                                @click="checkPromocode"
                            >
                                <template v-if="discount !== null">
                                    Отменить
                                </template>
                                <template v-else>Применить</template>
                            </button>
                            <p
                                class="rih-promo-input__error"
                                v-if="
                                    wrongPromo &&
                                    !disableBooking &&
                                    (step === 'first' ||
                                        (fio && formattedPhone && email))
                                "
                            >
                                *Неверный промокод
                            </p>
                        </div>
                        <button
                            v-if="step === 'second'"
                            class="rih-button rih-booking__prev"
                            @click="step = 'first'"
                        >
                            Назад
                        </button>
                        <button
                            class="rih-button rih-button--paddings rih-booking__btn"
                            :class="{
                                'rih-booking__btn--relative': step !== 'first',
                            }"
                            @click="clickBtn"
                            :disabled="disableBooking"
                        >
                            <template v-if="step === 'first'">
                                <span class="rih-button__main"
                                    >Следующий шаг</span
                                ><br /><span class="rih-button__additional"
                                    >данные клиента</span
                                >
                            </template>
                            <template v-else>
                                <span style="font-size: 16px"
                                    >Забронировать</span
                                ></template
                            >
                        </button>
                        <p class="rih-booking__error" v-if="disableBooking">
                            *Неверный промежуток аренды
                        </p>
                        <p
                            class="rih-booking__error"
                            v-if="step === 'second' && showPayloadError"
                        >
                            *Заполните все обязательные поля
                        </p>
                    </div>
                    <div class="rih-promo-input rih-promo-input--mobile-show">
                        <input
                            v-model="promocode"
                            placeholder="Введите промокод"
                            type="text"
                            class="rih-promo-input__input"
                            :disabled="discount !== null"
                        />
                        <button
                            class="rih-promo-input__submit"
                            @click="checkPromocode"
                        >
                            <template v-if="discount !== null">
                                Отменить
                            </template>
                            <template v-else>Применить</template>
                        </button>
                        <p class="rih-promo-input__error" v-if="wrongPromo">
                            *Неверный промокод
                        </p>
                    </div>
                    <rih-loading
                        v-if="calculating || booking || saving"
                        background
                        little
                    />
                </div>
            </div>
        </div>
        <rih-success-modal v-if="showSuccess" @close="$emit('back')" />
        <rih-error-modal
            v-if="showError"
            :error-text="errorText"
            @close="showError = false"
        />
    </div>
</template>

<script>
import RihLoading from "@/components/Loading";
import RihSuccessModal from "@/components/SuccessModal";
import RihErrorModal from "@/components/ErrorModal";
import moment from "moment";
import axios from "axios";
import humanizeDuration from "humanize-duration";
import { mapMutations, mapState } from "vuex";
export default {
    name: "RihBookingPage",
    components: { RihLoading, RihSuccessModal, RihErrorModal },
    props: {
        pickedInventory: Object,
        filterTimeStart: String,
        filterTimeEnd: String,
    },
    data() {
        return {
            calculating: false,
            range: {
                startDate: moment().format(),
                endDate: moment().add(1, "days").format(),
            },
            calculatedRent: null,
            showModal: false,
            timeStartValue: {
                HH: "12",
                mm: "00",
            },
            timeEndValue: {
                HH: "12",
                mm: "00",
            },
            rangeLocale: {
                firstDay: 1,
                daysOfWeek: ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"],
                monthNames: [
                    "Январь",
                    "Февраль",
                    "Март",
                    "Апрель",
                    "Май",
                    "Июнь",
                    "Июль",
                    "Август",
                    "Сентябрь",
                    "Октябрь",
                    "Ноябрь",
                    "Декабрь",
                ],
            },
            preferredCountries: [
                "RU",
                "BY",
                "KZ",
                "SK",
                "TR",
                "AM",
                "AZ",
                "PT",
                "ES",
                "AE",
                "UA",
            ],
            bookedDays: [],
            pickerKey: "picker",
            stopLoading: false,
            leftEnds: null,
            rightEnds: null,
            booking: false,
            step: "first",
            activeTariff: null,
            promocode: "",
            formattedPhone: null,
            fio: null,
            email: null,
            phone: null,
            discount: null,
            saving: false,
            showSuccess: false,
            wrongPromo: false,
            showError: false,
            errorText: "",
            partOfWeek: "",
            showPayloadError: false,
        };
    },
    computed: {
        ...mapState({
            TOKEN: (state) => state.token,
            MEDIA: (state) => state.media,
            DOMAIN: (state) => state.domain,
            needCart: (state) => state.needCart,
            cart: (state) => state.cart,
            colors: (state) => state.colors,
        }),

        prices() {
            return JSON.parse(JSON.stringify(this.pickedInventory.prices)).sort(
                (a, b) => a?.title > b?.title
            );
        },

        pricesObject() {
            const obj = {
                WEEKDAY_DAY: null,
                WEEKDAY_NIGHT: null,
                WEEKEND_DAY: null,
                WEEKEND_NIGHT: null,
            };

            this.prices.some((price) => {
                if (!price.title) return false;

                if (price.title.match(/.*будни.*/gi)) {
                    if (price.title.match(/.*день.*/gi)) {
                        obj.WEEKDAY_DAY = price;
                    } else if (price.title.match(/.*ночь.*/gi)) {
                        obj.WEEKDAY_NIGHT = price;
                    }
                } else if (price.title.match(/.*выходной.*/gi)) {
                    if (price.title.match(/.*день.*/gi)) {
                        obj.WEEKEND_DAY = price;
                    } else if (price.title.match(/.*ночь.*/gi)) {
                        obj.WEEKEND_NIGHT = price;
                    }
                }
            });

            return obj;
        },

        timeStart() {
            if (this.range.startDate === null) return null;
            const date = moment(this.range.startDate);
            if (!this.timeStartValue.HH || !this.timeStartValue.mm)
                return date.format();
            date.set({
                hour: this.timeStartValue.HH,
                minute: this.timeStartValue.mm,
                second: 0,
            });
            return date.format("YYYY-MM-DDTHH:mm:00ZZ");
        },

        timeEnd() {
            if (this.range.endDate === null) return null;
            const date = moment(this.range.endDate);
            if (!this.timeEndValue.HH || !this.timeEndValue.mm)
                return date.format();
            date.set({
                hour: this.timeEndValue.HH,
                minute: this.timeEndValue.mm,
                second: 0,
            });
            return date.format("YYYY-MM-DDTHH:mm:00ZZ");
        },

        sumRent() {
            if (this.calculatedRent === null) return "???";
            return this.calculatedRent.sum_total;
        },

        isBooked() {
            return this.cart.some(
                (inv) =>
                    moment(inv.time_start).isSame(moment(this.timeStart)) &&
                    moment(inv.time_end).isSame(moment(this.timeEnd)) &&
                    this.pickedInventory.id === inv.inventory_id
            );
        },

        minDate() {
            return moment().format();
        },

        hourRangeStart() {
            return this.getUnbookedHours(this.timeStart, true);
        },

        hourRangeEnd() {
            return this.getUnbookedHours(this.timeEnd, false);
        },

        minuteRangeStart() {
            return this.getUnbookedMinutes(this.timeStart, true);
        },

        minuteRangeEnd() {
            return this.getUnbookedMinutes(this.timeEnd, false);
        },

        disableBooking() {
            if (!Array.isArray(this.bookedDays) || this.bookedDays.length === 0)
                return false;
            return this.bookedDays.some((item) => {
                const startBetween = moment(this.timeStart).isBetween(
                    moment(item.time_start),
                    moment(item.time_end),
                    "minute",
                    "[]"
                );
                if (startBetween) return true;
                const endBetween = moment(this.timeEnd).isBetween(
                    moment(item.time_start),
                    moment(item.time_end),
                    "minute",
                    "[]"
                );
                if (endBetween) return true;
                const periodStartBetween = moment(item.time_start).isBetween(
                    moment(this.timeStart),
                    moment(this.timeEnd),
                    "minute",
                    "[]"
                );
                if (periodStartBetween) return true;
                const periodEndBetween = moment(item.time_end).isBetween(
                    moment(this.timeStart),
                    moment(this.timeEnd),
                    "minute",
                    "[]"
                );
                return periodEndBetween;
            });
        },

        durationRent() {
            return {
                timeStart: this.timeStart,
                timeEnd: this.timeEnd,
            };
        },

        isNightTariff() {
            return (
                this.activeTariff === this.pricesObject.WEEKEND_NIGHT ||
                this.activeTariff === this.pricesObject.WEEKDAY_NIGHT
            );
        },

        isDayTariff() {
            return this.activeTariff?.title === "Дневной";
        },

        wrongPayload() {
            return !this.fio || !this.formattedPhone || !this.email;
        },
    },
    watch: {
        hourRangeStart(hours) {
            if (
                Array.isArray(hours) &&
                hours.length > 0 &&
                !hours.includes(Number(this.timeStartValue.HH))
            ) {
                this.timeStartValue.HH = hours[0].toString().padStart(2, "0");
            }
        },

        hourRangeEnd(hours) {
            if (
                Array.isArray(hours) &&
                hours.length > 0 &&
                !hours.includes(Number(this.timeEndValue.HH))
            ) {
                this.timeEndValue.HH = hours[0].toString().padStart(2, "0");
            }
        },

        minuteRangeStart(minutes) {
            if (
                Array.isArray(minutes) &&
                minutes.length > 0 &&
                !minutes.includes(Number(this.timeStartValue.mm))
            ) {
                this.timeStartValue.mm = minutes[0].toString().padStart(2, "0");
            }
        },

        minuteRangeEnd(minutes) {
            if (
                Array.isArray(minutes) &&
                minutes.length > 0 &&
                !minutes.includes(Number(this.timeEndValue.mm))
            ) {
                this.timeEndValue.mm = minutes[0].toString().padStart(2, "0");
            }
        },

        durationRent() {
            if (moment(this.durationRent.timeStart).weekday() >= 4) {
                this.partOfWeek = "WEEKEND";
                if (Number(this.timeStartValue.HH) >= 21) {
                    this.activeTariff = this.pricesObject.WEEKEND_NIGHT;
                    this.timeEndValue.HH = "11";
                } else {
                    this.activeTariff = this.pricesObject.WEEKEND_DAY;
                }
            } else {
                this.partOfWeek = "WEEKDAY";
                if (Number(this.timeStartValue.HH >= 21)) {
                    this.activeTariff = this.pricesObject.WEEKDAY_NIGHT;
                    this.timeEndValue.HH = "11";
                } else {
                    this.activeTariff = this.pricesObject.WEEKDAY_DAY;
                }
            }
        },

        timeStart() {
            if (moment(this.timeStart).isAfter(moment(this.timeEnd))) {
                this.timeEndValue.HH = this.timeStartValue.HH;
                this.timeEndValue.mm = this.timeStartValue.mm;
            }
        },

        timeEnd() {
            if (moment(this.timeStart).isAfter(moment(this.timeEnd))) {
                this.timeStartValue.HH = this.timeEndValue.HH;
                this.timeStartValue.mm = this.timeEndValue.mm;
            }
        },

        // timeStartValue: {
        //     handler() {
        //         if (Number(this.timeStartValue.HH) >= 21) {
        //             this.activeTariff =
        //                 this.pickedInventory.prices[0].title === "Ночной"
        //                     ? this.pickedInventory.prices[0]
        //                     : this.pickedInventory.prices[1];
        //         }
        //     },
        //     deep: true,
        // },

        activeTariff() {
            if (this.isNightTariff) {
                if (this.timeStartValue.HH < 21 || this.timeStartValue.HH > 23)
                    this.timeStartValue = {
                        HH: "21",
                        mm: "00",
                    };
                this.timeEndValue = {
                    HH: "11",
                    mm: "00",
                };
            }

            if (this.isDayTariff) {
                this.timeStartValue = {
                    HH: "12",
                    mm: "00",
                };
            }

            this.calculateRent();
        },

        promocode() {
            this.wrongPromo = false;
        },
    },
    mounted() {
        this.setDatetimeFromFilter();
        this.activeTariff = this.pickedInventory?.prices[0];
    },
    methods: {
        ...mapMutations(["pushCart"]),

        getUnbookedMinutes(currentDate, checkNight) {
            const minutes = [...Array(60)].map((_, idx) => {
                return { time: idx, disable: false };
            });

            if (moment(currentDate).isSame(moment(), "hour"))
                for (let i = 0; i < moment().minutes(); i++)
                    minutes[i].disable = true;

            if (Array.isArray(this.bookedDays))
                this.bookedDays.some((range) => {
                    const isSameStart = moment(range.time_start).isSame(
                        moment(currentDate),
                        "hour"
                    );
                    const isSameEnd = moment(range.time_end).isSame(
                        moment(currentDate),
                        "hour"
                    );
                    const isBetween = moment(currentDate).isBetween(
                        moment(range.time_start),
                        moment(range.time_end),
                        "hour"
                    );

                    if (isBetween) {
                        minutes.forEach((item) => (item.disable = true));
                        return true;
                    }

                    if (!isSameStart && !isSameEnd) return false;

                    let minutesStart = moment(range.time_start).minutes();
                    let minutesEnd = moment(range.time_end).minutes();

                    const leftIter = isSameStart ? minutesStart : 0;
                    const rightIter = isSameEnd
                        ? minutesEnd
                        : minutes.length - 1;

                    for (let i = leftIter; i <= rightIter; i++)
                        minutes[i].disable = true;
                });

            if (
                checkNight &&
                this.isNightTariff &&
                this.timeStartValue.HH === "23"
            )
                for (let i = 1; i < 60; i++) minutes[i].disable = true;

            return minutes
                .filter((item) => !item.disable)
                .map((item) => item.time);
        },

        getUnbookedHours(currentDate, checkNight) {
            const hours = [...Array(24)].map((_, idx) => {
                return { time: idx, disable: false };
            });

            if (moment(currentDate).isSame(moment(), "day"))
                for (let i = 0; i < moment().hours(); i++)
                    hours[i].disable = true;

            if (Array.isArray(this.bookedDays))
                this.bookedDays.some((range) => {
                    const isSameStart = moment(range.time_start).isSame(
                        moment(currentDate),
                        "day"
                    );
                    const isSameEnd = moment(range.time_end).isSame(
                        moment(currentDate),
                        "day"
                    );
                    const isBetween = moment(currentDate).isBetween(
                        moment(range.time_start),
                        moment(range.time_end),
                        "day"
                    );

                    if (isBetween) {
                        hours.forEach((item) => (item.disable = true));
                        return true;
                    }

                    if (!isSameStart && !isSameEnd) return false;

                    let hoursStart = moment(range.time_start).hours();
                    if (moment(range.time_start).minutes() > 0) hoursStart += 1;
                    let hoursEnd = moment(range.time_end).hours();
                    if (moment(range.time_end).minutes() < 59) hoursEnd -= 1;

                    const leftIter = isSameStart ? hoursStart : 0;
                    const rightIter = isSameEnd ? hoursEnd : hours.length - 1;

                    for (let i = leftIter; i <= rightIter; i++)
                        hours[i].disable = true;
                });

            if (checkNight && this.isNightTariff)
                for (let i = 0; i < 21; i++) hours[i].disable = true;

            return hours
                .filter((item) => !item.disable)
                .map((item) => item.time);
        },

        changeTariff(price) {
            if (
                this.pricesObject.WEEKDAY_NIGHT === price ||
                this.pricesObject.WEEKEND_NIGHT === price
            )
                return;
            if (this.partOfWeek === "WEEKEND") {
                if (
                    this.pricesObject.WEEKDAY_DAY === price ||
                    this.pricesObject.WEEKDAY_NIGHT === price
                )
                    return;
            } else if (this.partOfWeek === "WEEKDAY") {
                if (
                    this.pricesObject.WEEKEND_DAY === price ||
                    this.pricesObject.WEEKEND_NIGHT === price
                )
                    return;
            }
            this.activeTariff = price;
        },

        setDatetimeFromFilter() {
            if (this.filterTimeStart) {
                const momentDate = moment(this.filterTimeStart);
                this.range.startDate = momentDate;
                this.timeStartValue.HH = momentDate.format("HH");
                this.timeStartValue.mm = momentDate.format("mm");
            }
            if (this.filterTimeEnd) {
                const momentDate = moment(this.filterTimeEnd);
                this.range.endDate = momentDate;
                this.timeEndValue.HH = momentDate.format("HH");
                this.timeEndValue.mm = momentDate.format("mm");
            }
        },

        addInCart() {
            this.calculatedRent.inventory_id = this.pickedInventory.id;
            this.pushCart(this.calculatedRent);
        },

        calculateRent() {
            if (this.timeEnd === null || this.timeStart === null) return;
            this.calculating = true;
            const discount = {};
            if (this.discount) {
                discount.discount = this.discount;
                discount.discount_id = this.discount.id;
            }
            const filter = {
                inventories: [
                    {
                        brokens: [],
                        can_kit_id: [],
                        count: 1,
                        finished: false,
                        inventory: this.pickedInventory,
                        inventory_id: this.pickedInventory.id,
                        kit_id: null,
                        kit_number: 0,
                        max: 1,
                        price: this.activeTariff,
                        price_id: this.activeTariff.id,
                        services: [],
                        sum: 0,
                        sum_amount_payment: 0,
                        sum_broken: 0,
                        sum_discount: 0,
                        sum_inventory: 0,
                        sum_one: 0,
                        sum_total: 0,
                        time_end: this.timeEnd,
                        time_start: this.timeStart,
                    },
                ],
                discounts: [discount],
                kits: [],
                need_calc_breaking: 1,
                need_calc_rent_delay: 0,
                payed: 0,
                payments: [],
                products: [],
                source_attraction_id: null,
                sum: 0,
                sum_additional_service: 0,
                sum_deposit: 0,
                sum_discount: 0,
                sum_product: 0,
                sum_real: 0,
                sum_rental: 0,
                sum_total: 0,
                time_end: this.timeEnd,
                time_start: this.timeStart,
            };

            if (this.pickedInventory.point) {
                filter.close_point = this.pickedInventory.point;
                filter.close_point_id = this.pickedInventory.point.id;
                filter.open_point = this.pickedInventory.point;
                filter.open_point_id = this.pickedInventory.point.id;
            }

            axios
                .post(
                    this.DOMAIN + "/v1/rent/recalc?token=" + this.TOKEN,
                    filter
                )
                .then((response) => {
                    if (
                        response.error === undefined &&
                        response.data &&
                        Array.isArray(response.data.array)
                    ) {
                        this.calculatedRent = response.data.array[0];
                    }
                })
                .finally(() => {
                    this.calculating = false;
                });
        },
        changeRange() {
            this.calculateRent();
        },
        loadBooking(date) {
            const year = String(date.getFullYear()).substr(2, 3);
            const month = String(date.getMonth() + 1).padStart(2, "0");
            this.booking = true;
            axios
                .post(
                    this.DOMAIN +
                        "/v2/widget/inventory_dates?token=" +
                        this.TOKEN,
                    {
                        inventory_id: this.pickedInventory.id,
                        year: year,
                        month: month,
                    }
                )
                .then((response) => {
                    if (response.error === undefined && response.data) {
                        this.bookedDays = response.data.cur;
                        this.$refs.picker.$forceUpdate();
                    }
                })
                .finally(() => {
                    this.booking = false;
                });
        },
        checkPromocode() {
            if (this.discount !== null) {
                this.discount = null;
                this.promocode = null;
                this.calculateRent();
            }
            axios
                .post(
                    this.DOMAIN +
                        "/v1/my/settings/discount/items?token=" +
                        this.TOKEN,
                    {
                        check_promocode: this.promocode,
                    }
                )
                .then((response) => {
                    const discount = response?.data?.array[0];
                    if (discount) {
                        this.discount = discount;
                        this.promocode = `Скидка ${discount.value}${
                            discount.discount_type_id === 1 ? "руб." : "%"
                        }`;
                        this.calculateRent();
                    } else {
                        this.wrongPromo = true;
                    }
                });
        },
        dateFormat(classes, date) {
            if (!classes.disabled) {
                const md = moment(date);
                let disabledFlag = false;
                let partiallyFlag = false;
                this.bookedDays.forEach((obj) => {
                    const timeStart = moment(obj.time_start);
                    const timeEnd = moment(obj.time_end);
                    const isStart = timeStart.isSame(md, "day");
                    const isEnd = timeEnd.isSame(md, "day");
                    const isBetween = md.isBetween(
                        timeStart,
                        timeEnd,
                        "day",
                        "()"
                    );
                    const isStartDay =
                        timeStart.format("mm:HH") === "00:00" && isStart;
                    const isEndDay =
                        timeEnd.format("mm:HH") === "23:59" && isEnd;

                    if (isStartDay || isEndDay || isBetween)
                        disabledFlag = true;

                    const isSmallRent =
                        moment
                            .duration(moment(timeEnd).diff(moment(timeStart)))
                            .days() < 2 &&
                        (isStart || isEnd);
                    if (
                        isSmallRent ||
                        (isStart && !isStartDay) ||
                        (isEnd && !isEndDay)
                    )
                        partiallyFlag = true;
                });
                if (disabledFlag) classes.disabled = disabledFlag;
                else if (partiallyFlag) classes.partially = partiallyFlag;
            }
            return classes;
        },
        changeNumber(obj) {
            if (obj.formattedNumber) this.formattedPhone = obj.formattedNumber;
        },
        createRent(rentObj) {
            rentObj.needs_send = true;
            axios
                .put(this.DOMAIN + "/v1/rent/edit?token" + this.TOKEN, rentObj)
                .then((response) => {
                    if (response.error === undefined && response.data) {
                        this.showSuccess = true;
                    } else {
                        const errorText = response?.data?.error;
                        if (errorText) {
                            this.showError = true;
                            this.errorText = errorText;
                        }
                    }
                })
                .catch((error) => {
                    const errorText = error?.response?.data?.error;
                    if (errorText) {
                        this.showError = true;
                        this.errorText = errorText;
                    }
                })
                .finally(() => {
                    this.saving = false;
                });
        },
        addRent() {
            if (this.wrongPayload) {
                this.showPayloadError = true;
                return;
            } else {
                this.showPayloadError = false;
            }

            this.saving = true;

            const [surname, name, patro] = this.fio.split(" ");
            const payload = {
                phone: this.formattedPhone,
                name: name,
                surname: surname,
                patro: patro,
                email: this.email,
            };
            axios
                .post(
                    this.DOMAIN + "/v1/h/client/phone?token=" + this.TOKEN,
                    payload
                )
                .then((response) => {
                    if (response.error === undefined && response.data) {
                        const payload = JSON.parse(
                            JSON.stringify(this.calculatedRent)
                        );
                        payload.client = response?.data?.human;
                        payload.human_id = payload.client.id;

                        this.createRent(payload);
                    } else {
                        const errorText = response?.data?.error;
                        if (errorText) {
                            this.showError = true;
                            this.errorText = errorText;
                        }
                    }
                })
                .catch((error) => {
                    const errorText = error?.response?.data?.error;
                    if (errorText) {
                        this.showError = true;
                        this.errorText = errorText;
                    }
                })
                .finally(() => {
                    this.saving = false;
                });
        },
        clickBtn() {
            if (this.step !== "second") {
                this.step = "second";
            } else {
                this.addRent();
            }
        },
        getDateRangeEnds(date) {
            return this.bookedDays.filter((item) => {
                return (
                    moment(item.time_start).isSame(moment(date), "day") ||
                    moment(item.time_end).isSame(moment(date), "day")
                );
            });
        },
        getDifference(d1, d2) {
            if (d1 === null || d2 === null) return "???";
            return this.humanize(moment.duration(moment(d2).diff(moment(d1))));
        },
        format(date) {
            if (date === null) return "???";
            return moment(date).format("DD.MM.YYYY HH:mm");
        },
        formatDate(date) {
            if (date === null) return "???";
            moment.locale("ru");
            return moment(date).format("D MMMM YYYY");
        },
        formatTime(date) {
            if (date === null) return "???";
            moment.locale("ru");
            return moment(date).format("HH:mm");
        },
        humanize(period) {
            const settings = {
                language: "ru",
                delimiter: " ",
                units: ["w", "d", "h", "m"],
            };
            return humanizeDuration(
                moment.duration(period).asMilliseconds(),
                settings
            );
        },
    },
    filters: {
        cutString(value, length = 20) {
            if (value.length > length) {
                value = value.substr(0, length) + "...";
            }
            return value;
        },
    },
};
</script>

<style lang="scss">
