        @media (max-width: 600px) {
            grid-column-start: 1;
            grid-column-end: 2;

            grid-row-start: 4;
            grid-row-end: 5;
        }
    }

    &__subheading {
        margin-bottom: 20px;
        margin-top: 0;
        color: var(--widget-text);
        font-weight: 700;
        font-size: 24px;
    }

    &__rent-datetime {
